import adsobject from "@/components/script/adsobject";

export default {
  name: "box",
  mixins: [adsobject],
  props: {
    ads_conf: undefined
  },
  created() {

  },
  computed: {
    image_url: function () {
      if (!this.isUrl(this.ads_conf['image'])) {
        return 'https://' + this.$config.ads_image_domains[0] + this.ads_conf['image'];
      }
      console.log('box image_url:');
      return this.ads_conf['image']
    }
  }
}
