import message_item from "@/components/user/notify/message_item";

export default {
  name: "notify",
  components: {
    message_item
  },
  data() {
    return {
      page_num: undefined,
      page: 1,
      message_list: [],
      total_count: undefined,
      PAGE_LIMIT:30
    }
  },
  created() {
    this.load_message_list(this.page);
  },
  methods: {
    on_page_change:function (e){
      this.pgae = parseInt(e);
      this.load_message_list(this.page);
    },
    load_message_list: function (page) {
      var start = ( parseInt(page) - 1) * this.PAGE_LIMIT;
      var limit = this.PAGE_LIMIT;
      this.$axios.get('/app/message/list', {
        params: {
          id: this.id,
          start:start,
          limit:limit
        }
      }).then((response) => {
        console.log("response:", response);
        this.message_list = response.data.messages;
        this.total_count = response.data.total_count;
      }).catch((error) => {
      });
    }
  }
}
